.add-option-error{
    color: $off-white;
    font-style: italic;
    margin: $m-size 0 0 0;
    padding: 0 $m-size;
}

.add-option{
    display: flex;
    padding: $m-size;
}

.add-option__input{
    flex-grow: 1;
    background: $dark-blue;
    color: $off-white;
    border: none;
    border-bottom: .3rem solid darken($color: #000000, $amount: 10);
    margin-right: $s-size;
    padding:$s-size;
}