.big-button{
    width: 100%;
    border: none;
    background-color: $purple;
    color: white;
    border-bottom: 0.6rem solid darken($color: $purple, $amount: 10);
    margin-bottom: $xl-size;
    padding: 2.4rem;
    font-weight: bold;
    font-size: $l-size;
}

.big-button:disabled{
    opacity: 0.5;
}

.button{
    background-color: $purple;
    border: none;
    border-bottom: .3rem solid darken($color: $purple, $amount: 10);
    color: white;
    font-weight: 500;
    padding: $s-size;
}

.button--link{
    background: none;
    border: none;
    padding: 0;
    color: $off-white;
}