.widget{
    background-color: $light-blue;
    margin-bottom: $xl-size;
}

.widget__message{
    border-bottom: 1px solid lighten($color: $light-blue, $amount: 10);
    color: $off-white;
    margin: 0;
    padding: $l-size;
    text-align: center;
}
.widget-header{
    background-color: $blue;
    display: flex;
    justify-content: space-between;
    color: $off-white;
    padding: $m-size;
    // margin: 0;
}

.widget-header__title{
    margin: 0;
}